.main_visual {
  max-width: 767px;
  width: 100%;
  font-family: 'Pretendard';
  color: #333;
  margin: 0 auto;
  position: relative;

  .bg {
    width: 100%;
    height: 48.46vw;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .logo {
    figure {
      position: absolute;
      top: 30px;
      left: 30px;
    }
  }

  .text {
    text-align: right;
    position: absolute;
    bottom: 6rem;
    right: 6rem;

    h1 {
      font-size: 11.5rem;
      padding-bottom: 6rem;
    }

    p {
      font-size: 2rem;
    }
  }
}

.main_visual .text h1::after {
  content: '';
  width: 60%;
  height: 2px;
  background-color: #333;
  position: absolute;
  bottom: 7rem;
  right: 0;
}

.section01 {
  max-width: 767px;
  width: 100%;
  font-family: 'Pretendard';
  color: #333;
  margin: 0 auto;
  padding: 50px 0;
  background-color: #F2EEE8;

  .inner {
    max-width: 767px;
    width: calc(100% - 60px);
    margin: 0 auto;

    ul {
      display: flex;
      gap: 20px;
      font-size: 2.5rem;
    }

    li.on,
    li:hover {
      text-decoration: 1px underline;
      text-underline-offset: 7px;
      cursor: pointer;
    }

    li.on a {
      display: block;
    }
  }
}

#root {
  .wrap {
    max-width: 767px;
    width: 100%;
    margin: 0 auto;
    background-color: #F2EEE8;
  }
}

.tour_list {
  max-width: 767px;
  width: calc(100% - 60px);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;


  .item {
    width: calc(100% / 2 - 10px);
    height: 100%;
    margin-bottom: 30px;
    position: relative;
    box-shadow: 0px 0px 5px #44444459;
    background-color: white;

    h4 {
      font-size: 2rem;
      margin-bottom: 15px;
      word-break: keep-all;
      font-family: 'Pretendard';
    }

    figure {
      height: 23.23vw;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;

      }
    }

    .item--text {
      padding: 2rem 10px;
      width: 100%;
      height: 130px;
      color: #333;
      font-family: 'Pretendard';
      line-height: 25px;

      p {
        font-size: 1.8rem;
        word-break: keep-all;
      }
    }
  }

}
@media screen and (max-width:1240px) {
  .main_visual {
    .bg {
      height: 69.46vw;
    }
  }
}
@media screen and (max-width:900px) {
  html,
  body {
    font-size: 50.00%;
  }
  .main_visual {
    .bg {
      height: 97.46vw;
    }
  }
}
@media screen and (max-width:767px) {
  html,
  body {
    font-size: 43.75%;
  }
  .main_visual {
    .bg {
      height: 97.46vw;
    }
    .text {
      h1 {
        font-size: 60px;
      }
    }
  }
}
@media screen and (max-width:480px) {
  .main_visual {
    .text {
      h1 {
        font-size: 55px;
      }
    }
  }
}
@media screen and (max-width:375px) {

  html,
  body {
    font-size: 37.50%;
  }

  .main_visual {
    .text {
      h1 {
        font-size: 50px;
      }
    }
  }

  .tour_list {
    .item {
      .item--text {
        line-height: normal;
        height: 100px;
      }
    }
  }
}