
@font-face {
  font-family: 'Pretendard';
  src: url('../public/font/Pretendard/Pretendard-SemiBold.eot');
  src: url('../public/font/Pretendard/Pretendard-SemiBold.eot?#iefix') format('embedded-opentype'),
      url('../public/font/Pretendard/Pretendard-SemiBold.woff2') format('woff2'),
      url('../public/font/Pretendard/Pretendard-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Pretendard';
  src: url('../public/font/Pretendard/Pretendard-ExtraBold.eot');
  src: url('../public/font/Pretendard/Pretendard-ExtraBold.eot?#iefix') format('embedded-opentype'),
      url('../public/font/Pretendard/Pretendard-ExtraBold.woff2') format('woff2'),
      url('../public/font/Pretendard/Pretendard-ExtraBold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

html, body {
  font-size: 62.5%;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
a {
  color: inherit;
  text-decoration: none;
}
